import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

@IonicPage()
@Component({
  selector: 'page-change-password02',
  templateUrl: 'change-password02.html',
})
export class ChangePassword02Page {

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {
  }

  goToWelCome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Change Password Successful");
  };

}



